
class Hangman {
    constructor(word, noOfGuess) {
        this.word = word.toLowerCase().split('');
        this.noOfGuess = noOfGuess;
        this.guessedLetters = [];
        this.status = 'playing';
    }

    get puzzel() {
        let puzzel = '';
    this.word.forEach( (w) => {
         if(this.guessedLetters.includes(w) || w == ' ') {
             puzzel += w;
         } else {
             puzzel += '*';
         }
        });
    

    return puzzel;
    }

    makeGuess(char) {
        if(this.status === 'playing') {
            char = char.toLowerCase();
            if(!this.guessedLetters.includes(char)) {
                this.guessedLetters.push(char);
                if(!this.word.includes(char)){
                    this.noOfGuess -= 1;
                }
            }
            this.calculateGameStatus();
        }
    }

    calculateGameStatus()  {
        
        if(this.noOfGuess === 0) {
            this.status = 'failed';
        } else if( !this.puzzel.includes('*')){
            this.status = 'finished';
        }
    }

    get statusMsg() {
            let msg;
        if(this.status === 'playing') {
            msg = `Playing --> Guess Left: ${this.noOfGuess}`;
        } else if ( this.status === 'failed') {
            msg = `Nice Try! The word was "${this.word.join('')}"`
        } else if ( this.status === 'finished') {
            msg = `Great Work! You guessed the word.`;
        }
        return msg;
    }

}

export { Hangman as default }










