import Hangman from './hangman';
import getPuzzle from './requests';


let hangman1;

const puzzelEl = document.querySelector('#puzzel');
const guessEl = document.querySelector('#guess');

// console.log(hangman1.status);

window.addEventListener('keypress', (e) => {

    const guess = String.fromCharCode(e.charCode);
    hangman1.makeGuess(guess);
    console.log(hangman1.status);
    render();


})

const render = () => {
    puzzelEl.innerHTML = '';
    //puzzelEl.textContent = `PUZZEL : ==>  ${hangman1.puzzel}`;

    hangman1.puzzel.split('').forEach((element) => {
        const spanEl = document.createElement('span');
       
            spanEl.textContent = element;
           
        
        puzzelEl.appendChild(spanEl);
    })
    guessEl.textContent = hangman1.statusMsg;
}

const gameStart = async () => {
    const puzzle = await getPuzzle();
    hangman1 = new Hangman(puzzle, 5);
    render();
}

gameStart();


document.querySelector('#reset').addEventListener("click", () => {
    gameStart();
})